.ChapterItem {
  display: flex;
  width: 100%;
  height: 100px;

  background-color: white;
  border-radius: 8px;

  img {
    min-width: 100px;
    max-width: 200px;

    height: 100%;
    object-fit: cover;
    border-radius: 8px 0 0 8px;
  }

  .content {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: space-between;
    padding: 20px 40px 20px 40px;

    h1 {
      font-size: 1.3rem;
      font-family: Lato-Regular;
      letter-spacing: 6px;
    }
    h2 {
      font-size: 1rem;
      font-family: Lato-Thin;
      font-weight: bold;
      letter-spacing: 6px;
    }
    h3 {
      font-size: 1rem;
      font-family: Lato-Thin;
      font-weight: bold;
    }
  }
}

@media screen and (max-width: 1024px) {
  .ChapterItem {
    img {
      max-width: 100px;
      object-fit: contain;
      background-color: rgb(211, 211, 211);
    }

    .content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;

      padding: 16px;
      h1 {
        letter-spacing: 0;
        font-size: 1.1rem;
        padding: 0;
        margin: 0;
      }

      h2 {
        letter-spacing: 3px;
        font-size: 0.9rem;
      }

      h3 {
        font-size: 0.8rem;
        margin: 3px 0 0 0;
      }
    }
  }
}
