// @import "../../themes/theme.scss";

.About {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;

  // background-color: $primary-color;
  background-image: url("../../assets/images/about-bg.jpg");
  background-color: black;
  background-size: cover;
  height: 100%;

  .content-wrapper {
    margin: auto 30px auto auto;
    width: 50%;
    color: white;

    z-index: 1;
    .content {
      // margin: 24px 0 0 0;
      min-width: 65%;
      // max-width: 550px;

      h1 {
        font-family: Lato-Regular;
        letter-spacing: 6px;

        margin: 0 0 20px 0;
        border-bottom: 5px solid #ffc875;
      }

      .description {
        font-size: 1.2rem;
        font-family: Lato-Regular;
        letter-spacing: 3px;
        word-spacing: 3px;
        line-height: 36pt;
        margin-top: 48px;
      }

      .social-media-text {
      }

      .social {
        display: flex;
        width: 70%;
        min-width: 420px;
        max-width: 500px;

        flex-direction: row;
        align-content: center;
        justify-content: space-between;

        margin-top: 20px;

        .user-content {
          display: flex;
          justify-content: space-between;

          .user {
            font-size: 1.2rem;
            font-family: Lato-Bold;
            letter-spacing: 3px;

            color: #73ffd0;
          }
          .divider {
            height: 32px;
            margin: 0 8px 0 8px;
            width: 1px;
            background-color: white;
          }
        }

        .social-button:hover {
          filter: brightness(60%) sepia(1);
          cursor: pointer;
          transition: 300ms;
        }
      }
    }
  }
  .highlight {
    color: #ffc875;
  }
}

@media screen and (max-width: 1024px) {
  .About {
    display: flex;
    .content-wrapper {
      margin: auto;
      width: 80%;
      padding: 80px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .About {
    .content-wrapper {
      .content {
        .social-media-text {
        }
        .social {
          display: flex;
          flex-direction: column;
          align-items: flex-start;

          justify-content: flex-start;

          .user-content {
            display: flex;
            width: 130px;
            justify-content: space-between;
          }
        }
      }
    }
  }
}
