@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Home {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgb(0, 0, 0);

  height: 100vh;

  overflow: hidden;

  .carousel {
    animation: fadeIn 1s ease-in;

    .carousel-bg {
      display: none;
    }
  }

  .bg-gradient {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    height: 100vh;
    background: linear-gradient(
      rgba(0, 0, 0, 0.3) 2%,
      transparent 80%,
      rgba(0, 0, 0, 0.7)
    );
  }

  .carousel-control-prev,
  .carousel-control-next {
    margin-top: 60px;
  }

  h3 {
    // text-transform: uppercase;
    font-size: 1.5rem;
    letter-spacing: 3px;
    font-family: Lato-Regular;
    font-weight: bold;
  }
  p {
    letter-spacing: 3px;
  }
}

@media screen and (max-width: 1024px) {
  .Home {
    .carousel {
      .read-chapter-button {
        display: none;
      }
      .carousel-bg {
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.5);
      }
      .carousel-caption {
        position: absolute;
        top: 25%;
        bottom: 25%;

        display: flex;
        flex-direction: column;

        margin-top: 60px;
        align-items: center;
        justify-content: center;
      }
    }

    .read-chapter-button {
      position: relative;
      bottom: 26%;
      padding: 6px;
      left: 0;
      right: 0;
      display: inline-block;
      background-color: #dcb374;
      margin: 0 auto 0 auto;
      color: white;
      height: 50px;
      width: 200px;
      border-radius: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      font-size: 0.8rem;
      letter-spacing: 3px;
      font-weight: bold;
      font-family: Lato-Regular;
      z-index: 998;

      user-select: none;

      animation: fadeIn 1s ease-in;
    }
    .read-chapter-button:hover {
      opacity: 0.7;
      transition: 300ms;
      cursor: pointer;
    }
  }
}
