$animate: all 250ms ease-in-out;

.active {
  color: lightblue;
}

.Navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;

  .nav-links {
    display: flex;
    flex: 1;
    padding: 16px;
    list-style: none;

    .nav-link-i {
      color: white;
      display: inline-block;
      margin: 0 16px 0 16px;
      font-size: 1.2rem;
      letter-spacing: 3px;

      text-decoration: none;
    }

    .nav-link-i::after {
      display: block;
      content: "";
      margin-top: 8px;
      width: 60%;
      transform-origin: 0% 50%;
      border-bottom: solid 3px white;
      transform: scaleX(0);
      transition: transform 250ms ease-in-out;
    }
    .nav-link-i:hover::after {
      transform: scaleX(1);
      transform-origin: 0% 50%;
    }

    // Nav link active class
    .nav-active {
      display: inline-block;
      pointer-events: none;
    }
    .nav-active::after {
      width: 60%;
      height: 1px !important;
      content: "" !important;
      transform: scaleX(1) !important;
      border-bottom: solid 3px white !important;
    }
  }
}

.NavbarMobile {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;

  color: white;
  z-index: 999;
  align-items: center;
  justify-content: space-between;
  padding: 16px 32px 16px 32px;

  .menu-logo {
    width: 70px;
    height: 70px;
    object-fit: contain;
  }
  .menu-logo:hover {
    opacity: 0.8;
    transition: 300ms;
    cursor: pointer;
  }

  .menu-icon {
    width: 40px;
    height: 40px;
  }
  .menu-icon:hover {
    cursor: pointer;
    opacity: 0.8;
    transition: 300ms;
  }

  .content {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    // padding: -16px;
    background-color: black;

    align-items: center;
    justify-content: center;

    ul {
      list-style: none;

      .nav-link-i {
        font-size: 2rem;
        color: white;
        text-transform: uppercase;
        text-decoration: none;
      }

      .nav-active {
        color: #dcb374;
        pointer-events: none;
      }

      li {
        margin: 8px;
        font-family: Lato-Regular;
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .Navbar {
    display: none;
  }

  .NavbarMobile {
    display: flex;
  }
}
