.ChapterDetailHeader {
  position: fixed;
  width: 100%;

  display: flex;
  flex-direction: column;
  background-color: #202020;

  opacity: 1;

  .header-container {
    display: flex;
    flex: 1;
    height: 60px;
    padding: 8px;

    .left-content {
      display: flex;
      align-items: center;
      z-index: 1;

      img {
        margin: 0 16px 0 8px;
        width: 50px;
        height: 100%;
        object-fit: contain;
        border-radius: 3px;
      }
      img:hover {
        cursor: pointer;
      }

      p {
        margin: 0;
        font-size: 0.8rem;
        color: white;
        letter-spacing: 3px;
        font-family: Lato-Thin;
        font-weight: bold;
      }
    }

    .center-content {
      display: flex;
      flex: 1;
      position: absolute;

      left: 0;
      right: 0;
      top: 0;
      bottom: 0;

      align-items: center;
      justify-content: center;

      h1 {
        font-size: 0.8rem;
        color: white;
        margin: 0 8px 0 8px;
      }

      .arrow-container {
        border-radius: 6px;
        background-color: #000000;
        padding: 6px 8px 6px 8px;
        border: 1px solid #303030;

        .arrow-right {
          width: 0;
          height: 0;
          border-top: 6px solid transparent;
          border-bottom: 6px solid transparent;

          border-left: 10px solid white;
        }
        :hover {
          cursor: pointer;
          opacity: 0.8;
          transition: 250ms;
        }

        .arrow-left {
          width: 0;
          height: 0;
          border-top: 6px solid transparent;
          border-bottom: 6px solid transparent;

          border-right: 10px solid white;
        }
        :hover {
          cursor: pointer;
          opacity: 0.8;
          transition: 250ms;
        }
      }

      .disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }
  }

  .mobile-chapter-controller {
    display: none;
  }

  .bg-color::before {
    opacity: 0;
    background-color: rgba(58, 58, 58, 0.9);
    transition: 300ms;
  }

  .bg-color {
    opacity: 1;
    background-color: rgba(58, 58, 58, 0.9);
    transition: 300ms;
  }

  .fade-in {
    opacity: 1;
    transition: 300ms;
  }

  .fade-out {
    display: none;
    opacity: 0;
    transition: 300ms;
  }
}

@media screen and (max-width: 1024px) {
  .ChapterDetailHeader {
    .header-container {
      .center-content {
        display: none;
      }
    }

    .mobile-chapter-controller {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 40px;
      padding: 8px;
      background-color: rgb(223, 223, 223);

      .arrow-container {
        border-radius: 6px;
        background-color: #000000;
        padding: 6px 8px 6px 8px;
        border: 1px solid #303030;

        .arrow-right {
          width: 0;
          height: 0;
          border-top: 6px solid transparent;
          border-bottom: 6px solid transparent;

          border-left: 10px solid white;
        }
        :hover {
          cursor: pointer;
          opacity: 0.8;
          transition: 250ms;
        }

        .arrow-left {
          width: 0;
          height: 0;
          border-top: 6px solid transparent;
          border-bottom: 6px solid transparent;

          border-right: 10px solid white;
        }
        :hover {
          cursor: pointer;
          opacity: 0.8;
          transition: 250ms;
        }
      }

      h1 {
        font-size: 0.8rem;
        color: white;
        margin: 0 8px 0 8px;
      }

      .disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }
  }
}
